@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;500&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Ubuntu', sans-serif;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

:root {
  --background-color: #DADDFC;
  --pink: #FC997C;
  --blue: #396EB0;
  --dark-blue: #2E4C6D;
}

.card {
    background: var(--blue);
    border-radius: 5px;
    overflow: hidden;
}

.avatar-container {
    overflow: hidden;
}

.avatar {
    width: 100%;
    transition: ease-in-out 200ms;
}

.avatar-container, .avatar {
    border-radius: 0 0 30% 30%;
}

.avatar:hover {
    transform: scale(1.2);
}

.login {
    padding: 1em;
    color: var(--pink);
}

.login a{
    text-decoration: none;
    color: #fff;
}
.controls {
    height: 10vh;
    background: var(--background-color);
    font-weight: 300;
    display: flex;
    justify-content: center;
    align-items: center;
}

ul {
    display: flex;
}

li {
    margin: 0 1em;
    list-style: none;
    font-size: 1em;
    width: 2em;
    height: 2em;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
}

.cur-page {
    background: var(--pink);
}

.icon {
    font-size: 2em;
}

li, .icon {
    cursor: pointer;
    color: var(--dark-blue);
}

li:active {
    color: var(--pink);
}
.loader {
    height: 100%;
    width: 100vw;
    background: var(--background-color);
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.preloader {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100px;
}

.dot {
    border-radius: 50%;
    width: 1em;
    height: 1em;
    background: var(--blue);
}

.dot1 {
    animation: 1000ms bounce ease-in-out infinite;
}

.dot2 {
    animation: 1000ms bounce 100ms ease-in-out infinite;
}

.dot3 {
    animation: 1000ms bounce 200ms ease-in-out infinite;
}

@keyframes bounce {
    0%   {transform: translateY(0);}
    50%  {transform: translateY(-2em);}
    100% {transform: translateY(0);}
}
.header {
    height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--dark-blue);
    color: var(--pink);
    font-size: 1rem;
}

.main-area {
    position: relative;
    min-height: 90vh;
    width: 100vw;
    overflow: auto;
    background: var(--background-color);
}

.content {
    min-height: 80vh;
}

.cards {
    display: grid;
    grid-gap: 10px;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    padding: 2em;
}
